'use client';

import { Bar, BarChart, CartesianGrid, XAxis, YAxis, ResponsiveContainer, Tooltip as RechartsTooltip } from 'recharts';
import type { Answers, Question, UserData, CompetencyGrade } from '@/lib/types';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from '@/components/ui/card';
import { Accordion, AccordionContent, AccordionItem, AccordionTrigger } from '@/components/ui/accordion';
import { ChartConfig, ChartContainer, ChartTooltipContent } from '@/components/ui/chart';
import { useMemo } from 'react';
import { Printer } from 'lucide-react';
import { Badge } from '@/components/ui/badge';
import { Tooltip, TooltipContent, TooltipProvider, TooltipTrigger } from '@/components/ui/tooltip';

type ReportProps = {
  userData: UserData;
  answers: Answers;
  summary: string;
  questions: Question[];
  grades: CompetencyGrade[];
  onStartOver: () => void;
};

export default function Report({ userData, answers, summary, questions, grades, onStartOver }: ReportProps) {

  const chartData = useMemo(() => {
    return questions.map(q => ({
      name: q.competency,
      "Longitud de Respuesta": answers[q.competency]?.length || 0,
    }));
  }, [questions, answers]);

  const chartConfig = {
    "Longitud de Respuesta": {
      label: "Longitud (caracteres)",
      color: "hsl(var(--primary))",
    },
  } satisfies ChartConfig;
  
  const handlePrint = () => {
    window.print();
  };

  const getGradeColor = (grade: string) => {
    if (grade.startsWith('A')) return 'bg-green-500';
    if (grade.startsWith('B')) return 'bg-blue-500';
    if (grade.startsWith('C')) return 'bg-yellow-500';
    if (grade.startsWith('D')) return 'bg-orange-500';
    if (grade.startsWith('F')) return 'bg-red-500';
    return 'bg-gray-500';
  };

  return (
    <>
      <CardHeader>
        <CardTitle className="font-headline text-2xl">Informe de Evaluación</CardTitle>
        <CardDescription>Gracias por completar la evaluación, {userData.name}. Aquí están tus resultados.</CardDescription>
      </CardHeader>
      <CardContent className="space-y-8">
        <Card className="bg-background">
          <CardHeader>
            <CardTitle className="text-xl font-headline">Resumen General por IA</CardTitle>
          </CardHeader>
          <CardContent>
            <p className="whitespace-pre-wrap text-muted-foreground">{summary}</p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader>
            <CardTitle className="text-xl font-headline">Calificación Conductual por Competencia</CardTitle>
            <CardDescription>Análisis de cada competencia basado en tus respuestas.</CardDescription>
          </CardHeader>
          <CardContent>
            <TooltipProvider>
              <ul className="space-y-4">
                {grades.map((gradeInfo, index) => (
                  <li key={index} className="flex flex-col sm:flex-row items-start sm:items-center justify-between rounded-lg border p-4">
                    <div className="flex-1 mb-2 sm:mb-0">
                      <p className="font-bold text-lg">{gradeInfo.competency}</p>
                      <p className="text-sm text-muted-foreground">{gradeInfo.justification}</p>
                    </div>
                    <div className="flex items-center gap-2">
                       <Tooltip>
                        <TooltipTrigger asChild>
                           <div className={`flex h-12 w-12 items-center justify-center rounded-full text-white font-bold text-lg ${getGradeColor(gradeInfo.grade)}`}>
                              {gradeInfo.grade}
                           </div>
                        </TooltipTrigger>
                        <TooltipContent>
                          <p>Puntaje: {gradeInfo.score.toFixed(1)} / 4.0</p>
                        </TooltipContent>
                      </Tooltip>
                    </div>
                  </li>
                ))}
              </ul>
            </TooltipProvider>
          </CardContent>
        </Card>
        
        <Card>
            <CardHeader>
                <CardTitle className="text-xl font-headline">Análisis de Longitud de Respuesta</CardTitle>
                <CardDescription>Este gráfico representa la longitud de tus respuestas, como un indicador de tu nivel de detalle.</CardDescription>
            </CardHeader>
            <CardContent>
                 <ChartContainer config={chartConfig} className="h-[300px] w-full">
                    <BarChart accessibilityLayer data={chartData} margin={{ top: 20, right: 20, bottom: 20, left: 20 }}>
                        <CartesianGrid vertical={false} />
                        <XAxis
                          dataKey="name"
                          tickLine={false}
                          tickMargin={10}
                          axisLine={false}
                          angle={-30}
                          textAnchor="end"
                          height={80}
                          interval={0}
                        />
                        <YAxis />
                        <RechartsTooltip cursor={false} content={<ChartTooltipContent indicator="dot" />} />
                        <Bar dataKey="Longitud de Respuesta" fill="var(--color-longitud-de-respuesta)" radius={4} />
                    </BarChart>
                </ChartContainer>
            </CardContent>
        </Card>

        <div>
          <h3 className="text-xl font-headline mb-4">Tus Respuestas en Detalle</h3>
          <Accordion type="single" collapsible className="w-full">
            {questions.map((q, index) => (
              <AccordionItem value={`item-${index}`} key={index}>
                <AccordionTrigger className="text-left">
                  <span className="font-medium">Competencia:</span>
                  <span className="font-normal text-muted-foreground ml-2">{q.question}</span>
                </AccordionTrigger>
                <AccordionContent>
                  <p className="whitespace-pre-wrap p-4 bg-muted/50 rounded-md text-muted-foreground">{answers[q.competency] || 'No se proporcionó respuesta.'}</p>
                </AccordionContent>
              </AccordionItem>
            ))}
          </Accordion>
        </div>
      </CardContent>
      <CardFooter className="flex justify-end gap-2">
        <Button onClick={handlePrint} variant="outline">
          <Printer className="mr-2 h-4 w-4" />
          Imprimir o Descargar
        </Button>
        <Button onClick={onStartOver} className="w-full sm:w-auto">
          Comenzar de Nuevo
        </Button>
      </CardFooter>
    </>
  );
}
